import catfood1  from '../assets/img/catfood1.jpg'
import catfood2  from '../assets/img/catfood2.jpg'
import catfood3  from '../assets/img/catfood3.jpg'
import catplaying1  from '../assets/img/catplaying1.jpg'
import catdressing1 from '../assets/img/catdressing1.jpg'
import catdressing2 from '../assets/img/catdressing2.jpg'
import cathousing1 from '../assets/img/cathousing1.jpg'
import cathousing2 from '../assets/img/cathousing2.jpg'
import cathousing3 from '../assets/img/cathousing3.jpg'



export default new Vuex.Store({
  state: {
    topRated:[
      {id: 0-1, name:'猫粮', price:100, url: catfood1, desc:"品牌猫粮", rate: 4.7 },
      {id: 0-2, name:'私人订制粮', price:299, url:catfood2, desc:"私人订制高品质", rate:4.6}

    ],

    allCategories: [
      {id: 1-1, name:'猫粮', price:100, url: catfood1, desc:"品牌猫粮", rate: 4.7 },
      {id: 1-2, name:'私人订制粮', price:299, url:catfood2, desc:"私人订制高品质", rate:4.6},
      {id: 1-3, name:'猫罐头', price:19.9, url:catfood3, desc:"补充营养好选择", rate: 4.5},
      {id: 1-4, name:'猫项圈', price:9.9, url:catdressing1, desc:"戴铃铛", rate: 4.8},
      {id: 1-5, name:'外带猫绳', price:19.9, url:catdressing2, desc:"带你的宠物遛弯必备", rate: 4.5},
      {id: 1-6, name:'猫窝1', price:59.9, url:cathousing1, desc:"舒适猫窝", rate: 4.8},
      {id: 1-7, name:'猫窝2', price:69.9, url:cathousing2, desc:"有型猫窝", rate: 4.7},
      {id: 1-8, name:'猫窝3', price:169.9, url:cathousing3, desc:"结实猫窝", rate: 4.5},
      {id: 1-9, name:'猫爬架', price:189.9, url:catplaying1, desc:"猫咪乐园", rate: 4.6},
    ],
    petfood:[
      {id: 2-1, name:'猫粮', price:100, url: catfood1, desc:"品牌猫粮", rate: 4.7 },
      {id: 2-2, name:'私人订制粮', price:299, url:catfood2, desc:"私人订制高品质", rate:4.6},
      {id: 2-3, name:'猫罐头', price:19.9, url:catfood3, desc:"补充营养好选择", rate: 4.5},
    ],
    petdressing:[
      {id: 3-1, name:'猫项圈', price:9.9, url:catdressing1, desc:"戴铃铛", rate: 4.8},
      {id: 3-2, name:'外带猫绳', price:19.9, url:catdressing2, desc:"带你的宠物遛弯必备", rate: 4.5},
    ],
    petplaying:[
      {id: 4-1, name:'猫爬架', price:189.9, url:catplaying1, desc:"猫咪乐园", rate: 4.6},

    ],
    pethousing:[
      {id: 5-1, name:'猫窝1', price:59.9, url:cathousing1, desc:"舒适猫窝", rate: 4.8},
      {id: 5-2, name:'猫窝2', price:69.9, url:cathousing2, desc:"有型猫窝", rate: 4.7},
      {id: 5-3, name:'猫窝3', price:169.9, url:cathousing3, desc:"结实猫窝", rate: 4.5},
    ],
    cartItemCount:0,
    cartItems:[]
  },
  mutations:{
    changeToCart(state, {payload, num}){
      let item = payload
      item = {...item, quantity:1}
      //确认购物车是否为空
      if(state.cartItems.length > 0){
        //确认商品是否在购物车里
        let bool = state.cartItems.some(i => i.id == item.id)
        if(bool){
          let itemIndex = state.cartItems.findIndex(el => el.id === item.id)
          
          state.cartItems[itemIndex]["quantity"] += num
          
          if(state.cartItems[itemIndex]["quantity"] == 0){
            // 如果购物车物品数量等于0则删除物品项
            state.cartItems.pop(item)
          }
        }else {
          //商品不在就直接加到购物车
          state.cartItems.push(item)
        }
      }else{
        // 购物车为空就直接添加
        state.cartItems.push(item)
      }
      state.cartItemCount+= num
    }
  },
  actions:{
    changeToCart:({ commit }, {payload, num}) =>{
      commit("changeToCart", {payload, num})
    }
  }
})

